import React from "react";

import { graphql, useStaticQuery } from "gatsby";

import useEmblaCarousel from "embla-carousel-react";
import Autoplay from "embla-carousel-autoplay";

import {
  IReviewQueryResult,
  IReviewSectionQueryResult,
} from "../../types/review.type";

import Border from "../../assets/common/border.svg";
import Review from "./review";
import TemplateSection from "../common/templateSection";
import { ILang } from "../../types/common.type";

const ReviewsSection = ({ lang }: ILang) => {
  const [emblaRef, emblaApi] = useEmblaCarousel(
    { loop: true, dragFree: true, duration: 30000, align: 0 },
    [
      Autoplay({
        delay: 0,
        stopOnInteraction: false,
        stopOnMouseEnter: true,
      }),
    ]
  );

  const reviewsData = useStaticQuery(query) as IReviewSectionQueryResult;

  const sectionMarkup = reviewsData?.markupData?.nodes?.find(
    (node) => node.fields.language === lang
  )?.frontmatter?.reviews;

  let reviews: IReviewQueryResult[] | null | undefined;

  if (lang in reviewsData)
    reviews = reviewsData?.[lang]?.nodes as
      | IReviewQueryResult[]
      | null
      | undefined;
  else reviews = reviewsData?.en?.nodes;

  return (
    <>
      {reviews ? (
        <>
          <Border className="w-full" />
          <TemplateSection
            fullWidth
            title={sectionMarkup?.title ?? "our clients' testimonials"}
            className="bg-secondary-100 text-main-100 w-full"
          >
            <div className="w-full overflow-hidden min-h-max" ref={emblaRef}>
              <div
                id="container"
                className="flex"
                onMouseEnter={() => {
                  emblaApi?.internalEngine().animation.stop();
                }}
                onMouseLeave={() => {
                  emblaApi?.internalEngine().animation.start();
                }}
              >
                {reviews.map((review) => (
                  <div
                    key={review.id}
                    className="flex shrink-0 min-w-0 cursor-pointer basis-full sm:basis-5/6 md:basis-4/6 lg:basis-1/2 xl:basis-2/5 3xl:basis-1/3 4xl:basis-1/4 rounded-2xl border-dashed border-main-10 mx-12 top-0 bottom-0"
                  >
                    {<Review review={review} />}
                  </div>
                ))}
              </div>
            </div>
          </TemplateSection>
          <Border className="w-full" />
        </>
      ) : null}
    </>
  );
};

export default ReviewsSection;

export const query = graphql`
  query {
    en: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "reviews" }, language: { eq: "en" } }
      }
    ) {
      nodes {
        id
        html
        frontmatter {
          company
          location
          person
          position
          image {
            childImageSharp {
              gatsbyImageData(
                height: 112
                placeholder: BLURRED
                formats: [AUTO, AVIF, WEBP]
              )
            }
          }
        }
      }
    }
    ru: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "reviews" }, language: { eq: "ru" } }
      }
    ) {
      nodes {
        id
        html
        frontmatter {
          company
          location
          person
          position
          image {
            childImageSharp {
              gatsbyImageData(
                height: 112
                placeholder: BLURRED
                formats: [AUTO, AVIF, WEBP]
              )
            }
          }
        }
      }
    }
    markupData: allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "common" } } }
      sort: { frontmatter: { order: ASC } }
    ) {
      nodes {
        fields {
          language
        }
        frontmatter {
          reviews {
            title
          }
        }
      }
    }
  }
`;
