import React from "react";

import { GatsbyImage, getImage } from "gatsby-plugin-image";

import { IReviewQueryResult } from "../../types/review.type";

const Review = ({
  review: {
    frontmatter: { company, person, position, location, image },
    html,
  },
}: {
  review: IReviewQueryResult;
}) => {
  const avatar = getImage(image);

  return (
    <div
      className="h-full flex flex-col grow p-8 text-main-100"
      itemProp="review"
      itemScope
      itemType="https://schema.org/Review"
    >
      <div
        className="mb-6 flex flex-nowrap justify-start items-center border-b-[4px] border-dashed border-secondary-100 gradient-border pb-10"
        itemProp="author"
        itemScope
        itemType="https://schema.org/Person"
      >
        <div
          className={`inline-block rounded-full h-32 w-32 flex items-center mr-10`}
        >
          {avatar ? (
            <GatsbyImage
              image={avatar}
              alt={company}
              className="h-full object-cover rounded-full h-32 w-32"
              itemProp="image"
            />
          ) : null}
        </div>
        <div>
          <h4
            className="text-xl font-extrabold whitespace-nowrap"
            itemProp="familyName"
          >
            {person}
          </h4>
          <meta itemProp="name" content={person} />
          <h5 className="text-xl font-extrabold whitespace-nowrap">
            <span itemProp="jobTitle">{position}</span>,{" "}
            <span itemProp="worksFor">{company}</span>
          </h5>
          <h5
            className="text-xl font-extrabold whitespace-nowrap"
            itemProp="workLocation"
          >
            {location}
          </h5>
        </div>
      </div>
      <div
        className="review-body shrink-0 text-2xl"
        itemProp="reviewBody"
        dangerouslySetInnerHTML={{ __html: html }}
      />
      <span
        itemProp="reviewRating"
        itemScope
        itemType="https://schema.org/Rating"
        className="hidden"
      >
        <meta itemProp="ratingValue" content="10" />
      </span>
    </div>
  );
};

export default Review;
