import React from "react";
import { useStaticQuery, graphql } from "gatsby";

import Border from "../../assets/common/border.svg";
import StackCarousel from "./stackCarousel";

const StackSection = () => {
  const techData = useStaticQuery(query);
  const techArr = techData?.markdownRemark?.frontmatter?.stack as
    | string[]
    | null;

  return (
    <>
      {techArr ? (
        <section className={`h-28 flex flex-col relative`} id="clients">
          <StackCarousel className="grow">
            {techArr.map((tech) => (
              <div
                key={tech}
                className="h-full shrink-0 min-w-0 cursor-pointer flex flex-col justify-between items-center content-center w-[352px] md:w-[400px] xl:w-[480px] 3xl:w-[576px]"
              >
                <Border className={`min-w-0 shrink-0 w-full`} />
                <p className="text-4xl font-extrabold whitespace-nowrap">
                  {tech}
                </p>
                <Border className={`min-w-0 shrink-0 w-full`} />
              </div>
            ))}
          </StackCarousel>
        </section>
      ) : null}
    </>
  );
};

export default StackSection;

export const query = graphql`
  query {
    markdownRemark(fields: { sourceName: { eq: "stack" } }) {
      frontmatter {
        stack
      }
    }
  }
`;
