import * as React from "react";
import type { HeadFC, HeadProps, PageProps } from "gatsby";

import { IconContext } from "@react-icons/all-files";
import "../styles/styles.css";

import Banner from "../components/common/banner";
import StackSection from "../components/stack/stackSection";
import ReviewsSection from "../components/reviews/reviewsSection";
import PostSection from "../components/posts/postSection";
import ProjectsSection from "../components/projects/projectsSection";
import { IIndexPageContext } from "../types/index.type";
import { BasicMetadata } from "../components/common/metadata";

const IndexPage = ({
  pageContext: { markupData, lang },
}: PageProps<null, IIndexPageContext>) => {
  return (
    <IconContext.Provider value={{ style: { verticalAlign: "middle" } }}>
      <Banner
        title={markupData.bannerTitle}
        text={markupData.bannerText}
        babylon
      />
      <StackSection />
      <ProjectsSection lang={lang} />
      <ReviewsSection lang={lang} />
      <PostSection lang={lang} />
    </IconContext.Provider>
  );
};

export default IndexPage;

export const Head: HeadFC<null, IIndexPageContext> = ({
  pageContext: { markupData },
}: HeadProps<null, IIndexPageContext>) => (
  <>
    <title>
      {markupData.head} | {markupData.bannerTitle}
    </title>
    <BasicMetadata />
  </>
);
